import { mapGetters, mapMutations } from 'vuex';
import eventListener from '@/eventBus';

export default {
	data() {
		return {
			
		};
	},
	
	onLoad() {
		this['SET_USERINFO']({
			name: '张三',
			sex: '男'
		});
		
		eventListener['TEST_ON'](() => {
			console.log('事件中转触发');
		});
	},
	
	onReady() {
		console.log(this.userInfo);
	},
	
	methods: {
		...mapMutations(['SET_USERINFO']),
				
		handleClick() {
			eventListener['TEST_EMIT']();
		}
	},
	
	computed: {
		...mapGetters(['userInfo'])
	}
